import React, { Component } from "react";
import WorkList from "./MyWorkList";
import '../assets/css/templatemo-style.css';
import { workData } from './WorkDataArray';

class MyWork extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedOption: "All"
    };
  }

  handleOptionChange = changeEvent => {
    this.setState({
      selectedOption: changeEvent.target.value
    }, () =>
      console.log(this.state.selectedOption));
    //const filteredWork = this.state.workData.filter(workData => {return workData.dataType})
  };

  render() {
    return (
      <form className="isotope-toolbar">
        <label>
          <input
            type="radio"
            data-type="All"
            value="All"
            checked={this.state.selectedOption === "All"}
            onChange={this.handleOptionChange}
            name="isotope-filter"
          />
          <span>all</span></label>
        <label
        ><input
            type="radio"
            data-type="React"
            value="React"
            checked={this.state.selectedOption === "React"}
            onChange={this.handleOptionChange}
            name="isotope-filter"
          />
          <span>React</span></label>
        <label
        ><input
            type="radio"
            data-type="HTML"
            value="HTML"
            checked={this.state.selectedOption === "HTML"}
            onChange={this.handleOptionChange}
            name="isotope-filter"
          />
          <span>HTML</span></label>
        <label
        ><input
            type="radio"
            data-type="CSS"
            value="CSS"
            checked={this.state.selectedOption === "CSS"}
            onChange={this.handleOptionChange}
            name="isotope-filter"
          />
          <span>CSS</span></label>
        <div className="isotope-box">
          <WorkList workData={workData} selected={this.state.selectedOption} />
        </div>
      </form>
    );
  }
}

export default MyWork;